import { useEffect } from 'react';
import Checkbox from '@/Components/Checkbox';
import GuestLayout from '@/Layouts/GuestLayout';
import InputError from '@/Components/InputError';
import InputLabel from '@/Components/InputLabel';
import PrimaryButton from '@/Components/PrimaryButton';
import TextInput from '@/Components/TextInput';
import { Head, Link, useForm } from '@inertiajs/react';
import { Divider } from 'antd';
import SecondaryButton from '@/Components/SecondaryButton';
// import BoxThree from '../../ReactFiber/Box.jsx'

export default function Login({ status, canResetPassword }) {
    const { data, setData, post, processing, errors, reset } = useForm({
        email: '',
        password: '',
        remember: false,
    });

    useEffect(() => {
        return () => {
            reset('password');
        };
    }, []);

    const submit = (e) => {
        e.preventDefault();

        post(route('login'));
    };

    return (
        <GuestLayout>
            <Head title="Log in" />

            {status && <div className="mb-4 font-medium text-sm text-green-600">{status}</div>}
            <div className="text-xs justify-end items-end mb-2">
                <div>
                    Admin: mavis@gmail.com
                </div>
                <div>
                    Password: password
                </div>
            </div>
            <form onSubmit={submit}>
                <div>
                    <InputLabel htmlFor="email" value="Email" />

                    <TextInput
                        id="email"
                        type="email"
                        name="email"
                        value={data.email}
                        className="mt-1 block w-full"
                        autoComplete="username"
                        isFocused={true}
                        onChange={(e) => setData('email', e.target.value)}
                    />

                    <InputError message={errors.email} className="mt-2" />
                </div>

                <div className="mt-4">
                    <InputLabel htmlFor="password" value="Password" />

                    <TextInput
                        id="password"
                        type="password"
                        name="password"
                        value={data.password}
                        className="mt-1 block w-full"
                        autoComplete="current-password"
                        onChange={(e) => setData('password', e.target.value)}
                    />

                    <InputError message={errors.password} className="mt-2" />
                </div>

                <div className="block mt-4">
                    <label className="flex items-center">
                        <Checkbox
                            name="remember"
                            checked={data.remember}
                            onChange={(e) => setData('remember', e.target.checked)}
                        />
                        <span className="ms-2 text-sm text-gray-600">Remember me</span>
                    </label>
                </div>

                <div className="mt-4">


                    {/* <PrimaryButton className="ms-4" disabled={processing}>
                        Register
                    </PrimaryButton> */}

                    <PrimaryButton className="flex items-center justify-center w-full hover:bg-white hover:text-gray-800 hover:rounded-md border-gray-800 hover:border-gray-500" disabled={processing}>
                        Log in
                    </PrimaryButton>

                    <div className="flex items-center justify-center mt-2">
                        {canResetPassword && (
                            <Link
                                href={route('password.request')}
                                className="underline text-sm text-gray-600 hover:text-gray-900 rounded-md focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                            >
                                Forgot your password?
                            </Link>
                        )}
                    </div>


                </div>
                <Divider> OR </Divider>

                <div className="flex items-center w-full justify-end mt-4">
                    <Link href={route('register')}  className="w-full">
                        <SecondaryButton className="flex items-center justify-center w-full hover:text-black hover:rounded-md hover:border-white-800" disabled={processing}>
                            Register
                        </SecondaryButton>
                    </Link>
                </div>
                <div className="text-sm text-red-500 italic flex justify-center items-center">
                    * This portfolio is still under development
                </div>
            </form>

            {/* <BoxThree /> */}

        </GuestLayout>
    );
}
